/* eslint-disable */
import React, { Component } from "react";
import GraphiQL from "graphiql";
import GraphiQLExplorer from "graphiql-explorer";
import { buildClientSchema, getIntrospectionQuery, parse } from "graphql";

import { makeDefaultArg, getDefaultScalarArgValue } from "./CustomArgs";

import "graphiql/graphiql.css";
import "./App.css";
import { DEFAULT_QUERY } from "./DefaultQueries";

import type { GraphQLSchema } from "graphql";

let retrivedObject = localStorage.getItem("testObject");
let testObject = JSON.parse(retrivedObject);
let urlValue = testObject?.urlValue;

const initialUrl = "https://my-graphqlmiddleware.glitch.me/graphql";
// const initialUrl = "https://my-graphqlmiddleware.glitch.me/graphql";

const defaultUrl = urlValue || initialUrl;

console.log("Starting of app=>", urlValue);

function fetcher(params: Object): Object {
  return fetch(defaultUrl, {
    method: "POST",
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json"
    },
    body: JSON.stringify(params)
  })
    .then(function (response) {
      return response.text();
    })
    .then(function (responseBody) {
      try {
        return JSON.parse(responseBody);
      } catch (e) {
        return responseBody;
      }
    });
}

type State = {
  schema: ?GraphQLSchema,
  query: string,
  explorerIsOpen: boolean
};

class App extends Component<{}, State> {
  _graphiql: GraphiQL;
  // state = {
  //   schema: null,
  //   explorerIsOpen: true,
  //   query: DEFAULT_QUERY,
  //   customUrl: defaultUrl,
  //   inputTextValue: defaultUrl
  // };
  /* sahil code below */
  constructor(props) {
    super(props);
    this.state = {
      schema: null,
      explorerIsOpen: true,
      query: DEFAULT_QUERY,
      customUrl: defaultUrl,
      inputTextValue: defaultUrl
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  handleChange(event) {
    this.setState({ inputTextValue: event.target.value });
  }

  handleSubmit(event) {
    let testObject = { urlValue: this.state.inputTextValue };
    // alert(
    //   "Url saved: " +
    //     this.state.inputTextValue +
    //     " Please refresh to get `Explore` show related queries."
    // );/* COMMENTED TEMPORARILY */
    localStorage.setItem("testObject", JSON.stringify(testObject));
    let retrivedObject = localStorage.getItem("testObject");
    let { urlValue } = JSON.parse(retrivedObject);
    console.log("Submitted: testObject.urlValue => ", urlValue);
    window.location.reload(); /* This is magic!! */

    event.preventDefault();
  }
  /* sahil code above */

  componentDidMount() {
    fetcher({
      query: getIntrospectionQuery()
    }).then((result) => {
      const editor = this._graphiql.getQueryEditor();
      editor.setOption("extraKeys", {
        ...(editor.options.extraKeys || {}),
        "Shift-Alt-LeftClick": this._handleInspectOperation
      });
      this.setState({ schema: buildClientSchema(result.data) });
    });
  }

  _handleInspectOperation = (
    cm: any,
    mousePos: { line: Number, ch: Number }
  ) => {
    const parsedQuery = parse(this.state.query || "");

    if (!parsedQuery) {
      console.error("Couldn't parse query document");
      return null;
    }

    var token = cm.getTokenAt(mousePos);
    var start = { line: mousePos.line, ch: token.start };
    var end = { line: mousePos.line, ch: token.end };
    var relevantMousePos = {
      start: cm.indexFromPos(start),
      end: cm.indexFromPos(end)
    };

    var position = relevantMousePos;

    var def = parsedQuery.definitions.find((definition) => {
      if (!definition.loc) {
        console.log("Missing location information for definition");
        return false;
      }

      const { start, end } = definition.loc;
      return start <= position.start && end >= position.end;
    });

    if (!def) {
      console.error(
        "Unable to find definition corresponding to mouse position"
      );
      return null;
    }

    var operationKind =
      def.kind === "OperationDefinition"
        ? def.operation
        : def.kind === "FragmentDefinition"
        ? "fragment"
        : "unknown";

    var operationName =
      def.kind === "OperationDefinition" && !!def.name
        ? def.name.value
        : def.kind === "FragmentDefinition" && !!def.name
        ? def.name.value
        : "unknown";

    var selector = `.graphiql-explorer-root #${operationKind}-${operationName}`;

    var el = document.querySelector(selector);
    el && el.scrollIntoView();
  };

  _handleEditQuery = (query: string): void => this.setState({ query });

  _handleToggleExplorer = () => {
    this.setState({ explorerIsOpen: !this.state.explorerIsOpen });
  };

  render() {
    const { query, schema } = this.state;
    return (
      <div>
        <div className="graphiql-container">
          <GraphiQLExplorer
            schema={schema}
            query={query}
            onEdit={this._handleEditQuery}
            onRunOperation={(operationName) =>
              this._graphiql.handleRunQuery(operationName)
            }
            explorerIsOpen={this.state.explorerIsOpen}
            onToggleExplorer={this._handleToggleExplorer}
            getDefaultScalarArgValue={getDefaultScalarArgValue}
            makeDefaultArg={makeDefaultArg}
          />
          <GraphiQL
            ref={(ref) => (this._graphiql = ref)}
            fetcher={fetcher}
            schema={schema}
            query={query}
            onEditQuery={this._handleEditQuery}
          >
            <GraphiQL.Toolbar>
              <GraphiQL.Button
                onClick={() => this._graphiql.handlePrettifyQuery()}
                label="Prettify"
                title="Prettify Query (Shift-Ctrl-P)"
              />
              <GraphiQL.Button
                onClick={() => this._graphiql.handleToggleHistory()}
                label="History"
                title="Show History"
              />
              <GraphiQL.Button
                onClick={this._handleToggleExplorer}
                label="Explorer"
                title="Toggle Explorer"
              />
            </GraphiQL.Toolbar>
          </GraphiQL>
        </div>
        <form onSubmit={this.handleSubmit}>
          <label>
            Graphql Endpoint Url:
            <input
              type="text"
              value={this.state.inputTextValue}
              onChange={this.handleChange}
            />
          </label>
          <strong>
            <input
              class="inputSubmitButton"
              type="submit"
              value="Update graphiql's environment"
            />
          </strong>
          Tip: You can press Enter to upate too.
        </form>
        <button
          /* are you .......... */
          onClick={() => {
            localStorage.setItem(
              "testObject",
              JSON.stringify({ urlValue: initialUrl })
            );
            window.location.reload();
          }}
        >
          Reset App
        </button>
      </div>
    );
  }
}

export default App;
